import { ActivatedRoute, Router } from '@angular/router';
import { ItemComplaint } from '@biz-nest/commerce-client';
import { FrontendLogger } from '@biz-nest/core-client';
import {
  EventbusService, MainBarTitleService,
  ViewOneCompose
} from '@front-shared-lib';
import { navByBus } from '@main-shared-lib';

export abstract class ViewItemComplaintBase {
  viewOneCompose = new ViewOneCompose(
    ItemComplaint,
    { route: [''], eventName: '' },
    this.router,
    this.route,
    this.eventbus
  );

  title = 'View';

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;


  constructor(
    protected router: Router,
    protected route: ActivatedRoute,
    protected eventbus: EventbusService,
    public mainBarTitle: MainBarTitleService
  ) { }

  async init() {
    try {
      await this.viewOneCompose.init();

      this.title = 'View';

      this.mainBarTitle.title = 'View EcommerceOrder Refund - ' + this.title;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  abstract closeModal(): void;


  destroy() {
    this.viewOneCompose.cleanup?.();
  }
}

