import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import {
  EventbusService, MainBarTitleService,
  openClose,
  ViewItemComplaintBase
} from '@front-shared-lib';

@Component({
  selector: 'app-view-item-complaint',
  standalone: true,
  imports: [
    RouterModule
  ],
  animations: [
    openClose
  ],
  templateUrl: './view-item-complaint.component.html',
  styleUrl: './view-item-complaint.component.scss'
})
export class ViewItemComplaintComponent
  extends ViewItemComplaintBase
  implements OnInit, OnDestroy {
  constructor(
    protected override router: Router,
    protected override route: ActivatedRoute,
    protected override eventbus: EventbusService,
    public override mainBarTitle: MainBarTitleService
  ) {
    super(router, route, eventbus, mainBarTitle);
  }

  ngOnInit() {
    this.init();
  }

  ngOnDestroy() {
    this.destroy();
  }

  closeModal(): void {
    this.eventbus.generalEvent$.next({
      type: 'closeViewItemComplaintModal'
    });
  }
}

