import { Meta, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { ItemComplaint } from '@biz-nest/commerce-client';

import {
  AppNameService,
  EventbusService, ListComposeAdminPanel,
  MainBarTitleService,
  scrollToTop, WindowService
} from '@front-shared-lib';
import { navByBus } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';


export abstract class ItemComplaintListBase {
  posting = false;
  cleanup?: () => void;
  listCompose = new ListComposeAdminPanel<ItemComplaint>(
    ItemComplaint,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  actions = [
    {
      label: 'View',
      icon: 'visibility',
      event: (itemComplaint: ItemComplaint) => this.viewItemComplaint(itemComplaint)
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (itemComplaint: ItemComplaint) => this.deleteItemComplaint(itemComplaint)
    },
    {
      label: 'Update',
      icon: 'edit',
      event: (itemComplaint: ItemComplaint) => this.updateItemComplaint(itemComplaint)
    }
  ];

  constructor(
    protected windowService: WindowService,
    protected toastr: ToastrService,
    protected router: Router,
    protected eventbus: EventbusService,
    protected apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    protected metaTagService: Meta,
    protected titleService: Title
  ) { }

  init(): void {
    this.listCompose.getDataArr();
    this.mainBarTitle.title = 'Item Complaints';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: `Efficiently manage and comprehensively monitor your automatic item taxes,
      empowering you to maintain financial clarity and make informed decisions
      regarding your fiscal responsibilities and opportunities for savings.`
    });

    scrollToTop(this.windowService);
  }

  abstract viewItemComplaint(itemComplaint: ItemComplaint): void;

  abstract deleteItemComplaint(itemComplaint: ItemComplaint): void;

  abstract updateItemComplaint(itemComplaint: ItemComplaint): void;

  destroy(): void {
    this.listCompose.cleanup?.();
  }
}
